  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  overflow: hidden;
  height: 50px;
  background: #fff;
  border-top: 1px solid #e4e4e4;
}

.m-tabbar-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.m-tabbar-item .m-tabbar-item-icon {
  display: block;
  padding-top: 5px;
}
.m-tabbar-item .m-tabbar-item-icon img {
  width: 20px;
  height: 20px;
}
