.m-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 44px;
  padding: 0 10px;
  background: #fff;
  color: #494949;
  position: relative;
}
.m-header:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  background: #e4e4e4;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
